/* Navbar */
.navbar {
  background-color: #1f1f2f; /* Default dark navbar */
  height: 70px; /* Full height when sidebar is expanded */
  width: 100%; /* Full width by default */
  transition: background-color 0.3s, height 0.3s, width 0.3s; /* Transition for width */
}
.navbarLarge {
  background-color: #007bff; /* Large navbar background color */
  height: 70px; /* Large navbar height */
}

.navbarSmall {
  background-color: #1f1f2f; /* Smaller navbar background color */
  height: 50px; /* Smaller navbar height */
}

/* Logo */
.logo {
  font-size: 1.5rem;
  color: white;
  display: flex;
  align-items: center;
}

.logoImg {
  width: 40px;
  margin-right: 10px;
}

.navIcon {
  color: white;
  font-size: 1.2rem;
  margin-left: 20px;
}
